import {
  Component,
  HostListener,
  Inject,
  OnInit,
  trigger, state, style, transition, animate
} from '@angular/core';
import { DOCUMENT } from '@angular/platform-browser';
import { FormControl } from '@angular/forms';
import 'rxjs/add/operator/debounceTime';

import { FaHttpService } from "../fa-http.service";

@Component({
  selector: 'app-fa-list',
  templateUrl: './fa-list.component.html',
  styleUrls: ['./fa-list.component.css'],
  animations: [
    trigger('footer', [
      state('invisible', style({ transform: 'translateY(100%)' })),
      state('visible', style({ transform: 'translateY(0)' })),
      transition('invisible => visible', animate('500ms ease-out')),
      transition('visible => invisible', animate('300ms ease-out')),
    ])
  ]
})
export class FaListComponent implements OnInit {

  tags = [ 'ロゴ', '矢印', 'カーソル' ];

  datas;
  results;
  tokenizer;
  clipboard;
  isLoaded: boolean = false;
  isVisible: string = 'invisible';
  input;

  model = { text: '', type: 'and' };

  searchInput = new FormControl();
  footerSearchInput = new FormControl();

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private faHttpService: FaHttpService
  ) {

    // Load and prepare tokenizer
    window['kuromoji'].builder({ dicPath: './dict/' }).build((error, _tokenizer) => {
      if (error != null) {
        console.log(error);
      }
      this.tokenizer = _tokenizer;
      this.isLoaded = true;

      setTimeout(() => {
        this.input = document.getElementById('search-text');
        this.input.focus();
      }, 300);

    });

    // clipboard
    this.clipboard = new window['Clipboard']('.copy', {
      text: (trigger) => {
        // TODO: optionを選択できるようにする
        return `<i class="fa ${trigger.getAttribute('data-name')}"></i>`;
      }
    });
    this.clipboard.on('success', function(e) {
      //console.info('Action:', .action);
      //console.info('Text:', e.text);
      //console.info('Trigger:', e.trigger);

      //e.clearSelection();
      e.trigger.innerHTML = 'clip!';

      setTimeout(() => {
        e.trigger.innerHTML = 'copy';
      }, 1000);
    });

    this.searchInput.valueChanges
      .debounceTime(300)
      .subscribe(text => {
        if (text === '') {
          this.results = this.datas;
        } else {
          let tokenized = this.tokenizer.tokenize(this.formatString(text));
          this.search(tokenized);
        }
      });

  }


  ngOnInit() {

    this.faHttpService.getData()
      .then(data => {
        this.datas = data;
        this.results = data;
      });

  }

  formatString(str) :string {
    let text = '';
    text = str.replace(/\s/g, ',').toLowerCase();

    return text;
  }

  onSelectSearchType(event) {
    this.model.type = event.target.value;
  }

  onClickTag(event, tag) {
    this.model.text = tag;
  }

  submit() {
    if (this.model.text === '') return;
    let tokenized = this.tokenizer.tokenize(this.formatString(this.input.value));
    this.search(tokenized);
  }

  /**
   * カンマの分かち書きだけを削除
   * @param tokenized
   * @returns {any}
   */
  deleteSeparater(tokenized) {
    let data;
    data = tokenized.filter((token) => {
      return token.surface_form !== ',';
    });

    return data;
  }

  /**
   * 検索
   * @param _tokenized 分かち書き結果
   */
  search(_tokenized) {

    const tokenized = this.deleteSeparater(_tokenized);

    let filtered = this.datas.filter((data, i) => {
      let isMatch = false;
      let checked = [];

      // AND検索
      if (this.model.type === 'and') {
        tokenized.forEach((token:any) => {
          if ((~data.name.indexOf(token.surface_form) ||
            ~data.keywords.indexOf(token.surface_form)) ||
            (token.reading && ~data.keywords.indexOf(token.reading))) {
            checked.push(true);
          }
        });
        if (checked.length === tokenized.length) isMatch = true;
      // OR検索
      } else if (this.model.type === 'or') {
        tokenized.some((token:any) => {
          if (~data.name.indexOf(token.surface_form) ||
            ~data.keywords.indexOf(token.surface_form) ||
            (token.reading && ~data.keywords.indexOf(token.reading))) {
            isMatch = true;
            return false;
          }
        });
      }

      if (isMatch) return data;

    });

    this.results = filtered;
  }

  @HostListener('window:scroll', [])
  onScroll() {
    let scrollTop = this.document.body.scrollTop;
    if (scrollTop > 100) {
      this.isVisible = 'visible';
    } else if (this.isVisible === 'visible' && scrollTop > 10) {
      this.isVisible = 'invisible';
    }

  }
}



